<template>
    <div>
        <van-nav-bar
        title="欢迎你回来"
        left-text="返回"
        right-text="注册"
        left-arrow
        class="style-black"
        />
        <span>正在登录中...</span>
        
    </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
    name:'login',
    created(){
        //调用微信登录状态  先判断是否为微信登录环境和开发环境 
        this.$selfLoading(undefined,{
            isOpacity:false
        });
    }
}
</script>
<style lang="less">
    .van-nav-bar.style-black{
        background-color: #000000;
        .van-icon{
            color:#e6ac01;
        }
        .van-nav-bar__text{
            color: #e6ac01;
        }
        .van-nav-bar__title{
            color: #e6ac01;
        }
    }
</style>




